export default{
	data(){
		return{
			winSize:false,///* 显示遮罩 */
			windowHeight:0,//屏幕高度
			Nodata:false,//有无数据
			getListData:[{
				image:'../../../static/demo14.png',
				name:'超松数码专营店',
				bean:'124',
				popu:false
			},{
				image:'../../../static/demo14.png',
				name:'超松数码专营店',
				bean:'23432',
				popu:false
			}]
		}
	},
	onLoad() {
		var that = this
		uni.getSystemInfo({
		    success: function (res) {
				that.windowHeight = res.windowHeight
		        console.log('屏幕高度为'+res.windowHeight);
		    }
		});
	},
	methods:{
		//点击删除
		onLongPress(key){
			var that = this
			that.getListData[key].popu = true
			that.winSize = true
			
		},
		//进店逛逛
		enterClick(id){
			uni.navigateTo({
				url:'../../ShopDetails/StoreDetails/storedetails'
			})
		},
		//删除记录
		delListClick(){
			this.winSizeClick()
		},
		//点击遮罩
		winSizeClick(){
			var that = this
			that.winSize = false
			that.getListData.forEach(function(item){
				item.popu = false
			})
		},
	}
}
